.page-header {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 62px;
  background: -moz-linear-gradient(50% 100% 90deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .6) 100%);
  background: -webkit-linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .6) 100%);
  background: -webkit-gradient(linear, 50% 100%, 50% 0%, color-stop(0, rgba(0, 0, 0, 0)), color-stop(1, rgba(0, 0, 0, .6)));
  background: -o-linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .6) 100%);
  background: -ms-linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .6) 100%);
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .6) 100%);

  @media (min-width: $medium-breakpoint) {
    height: 90px;
  }

  @media (min-width: $large-breakpoint) {
    height: 108px;
  }

  &__inner {
    @mixin container;

    padding-top: 1rem;

    @media (min-width: $medium-breakpoint) {
      padding-top: 1.5rem;
    }
  }
}
