.popup-menu {
  padding-left: 30px;
  padding-bottom: 18px;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

  @media (min-width: 600px) {
    padding-left: 0;
  }

  @media (min-width: $medium-breakpoint) {
    padding-left: 56px;
  }

  &__item {
    padding-bottom: 27px;

    @media (min-width: $medium-breakpoint) {
      padding-bottom: 53px;
    }

    &--active {

      .popup-menu__link {
        color: rgba(0, 0, 0, .5);
      }
    }
  }

  &__link {
    font-family: $ptsans;
    font-size: 1.5rem;
    color: #fff;
  }
}
