.docs-hero {
  height: 289px;
  background: url(signing-documents.jpg) no-repeat center;
  background-size: cover;
  position: relative;

  @media (min-width: $medium-breakpoint) {
    height: 480px;
  }

  @media (min-width: $large-breakpoint) {
    height: 576px;
  }

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, .4);
  }

  &__inner {
    @mixin container;

    padding-top: 146px;

    @media (min-width: $medium-breakpoint) {
      padding-top: 253px;
    }

    @media (min-width: $large-breakpoint) {
      padding-top: 297px;
    }
  }

  &__caprion {
    font-family: $ptsans-caption;
    color: #fff;
    position: relative;
    z-index: 1;
    font-weight: 600;
    font-size: 30px;
    line-height: 33px;

    @media (min-width: $medium-breakpoint) {
      font-size: 45px;
      line-height: 49.5px;
    }

    @media (min-width: $large-breakpoint) {
      font-size: 54px;
      line-height: 59.5px;
    }
  }

  &__description {
    font-family: $ptserif;
    position: relative;
    z-index: 1;
    color: $colder;
    margin-top: 12px;
    font-size: 13px;
    line-height: 17px;

    &.hide-for-small {
      display: none;
    }

    @media (min-width: $medium-breakpoint) {
      font-size: 20px;
      line-height: 34px;
      margin-top: 20px;

      &.hide-for-small {
        display: block;
      }
    }
  }
}
