.payment-block {
  position: relative;
  z-index: 1;
  height: 42px;
  display: flex;
  align-items: center;

  @media (min-width: $medium-breakpoint) {
    height: 50px;
  }

  @media (min-width: $large-breakpoint) {
    height: 58px;
  }

  &__input {
    display: none;
    flex-shrink: 0;

    @media (min-width: $medium-breakpoint) {
      display: inline-block;
      margin-right: 16px;
      width: 170px;

      .input-block__input {
        font-weight: 600;
        font-size: 28px;
        letter-spacing: 2.8px;
      }
    }

    @media (min-width: $large-breakpoint) {
      margin-right: 36px;
      width: 205px;
    }
  }

  &__dropdown {
    display: none;

    @media (min-width: $medium-breakpoint) {
      display: inline-block;
      margin-right: 16px;
      position: absolute;
      top: 5px;
      left: 187px;
    }

    @media (min-width: $large-breakpoint) {
      margin-right: 36px;
      left: 241px;
    }
  }

  &__btn-wrap {
    display: flex;
    justify-content: center;
    width: 182px;
    margin-left: -5px;

    @media (min-width: $medium-breakpoint) {
      margin-left: 195px;
      width: 240px;
    }

    @media (min-width: $large-breakpoint) {
      margin-left: 256px;
      width: 264px;
    }
  }

  &__btn {
    @media (min-width: $medium-breakpoint) {
      margin-left: -5px;
    }
  }
}
