.volunteer {
  background: $colder;

  @media (min-width: $medium-breakpoint) {
    background: $colder url(nd-pattern.svg);
    background-size: 2560px 1821px;
  }

  &__inner {
    @mixin container;

    padding-top: 39px;
    padding-bottom: 39px;

    @media (min-width: $medium-breakpoint) {
      padding-top: 70px;
      padding-bottom: 63px;
    }

    @media (min-width: $large-breakpoint) {
      padding-top: 75px;
      padding-bottom: 65px;
    }
  }

  &__title {
    width: 200px;

    @media (min-width: $medium-breakpoint) {
      width: 100%;
    }
  }

  &__heading {
    margin-top: 1.5rem;
  }

  &__text {
    margin-top: 1rem;

    @media (min-width: $large-breakpoint) {
      margin-top: 0.6rem;
    }
  }

  &__btn-wrap {
    margin-top: 2rem;
    display: flex;
    align-items: center;

    @media (min-width: $medium-breakpoint) {
      margin-top: 42px;
      height: 50px;
    }

    @media (min-width: $large-breakpoint) {
      margin-top: 38px;
      height: 58px;
    }
  }

  &__btn-inner {
    display: none;

    @media (min-width: $medium-breakpoint) {
      display: flex;
      justify-content: center;
      width: 275px;
      margin-left: 60px;
    }

    @media (min-width: $large-breakpoint) {
      margin-left: 67px;
      width: 330px;
    }
  }
}
