.partners {
  @media (min-width: $medium-breakpoint) {
    background: url(nd-pattern.svg);
    background-size: 2560px 1821px;
  }

  &__background {
    position: absolute;
    height: 190px;
    width: 100%;
    background: linear-gradient(to top, rgba(255, 255, 255, 1) 25%, rgba(255, 255, 255, 0) 100%), url(hands.jpg);
    background-repeat: no-repeat;
    background-size: 130%;
    background-position: center;
    z-index: 1;

    @media (min-width: 480px) {
      height: 260px;
    }

    @media (min-width: $medium-breakpoint) {
      height: 360px;
    }
  }

  &__inner {
    @mixin container;

    padding-top: 96px;

    @media (min-width: 480px) {
      padding-top: 120px;
    }

    @media (min-width: $medium-breakpoint) {
      padding-top: 160px;
    }

    @media (min-width: $large-breakpoint) {
      padding-top: 192px;
    }
  }

  &__title {
    position: relative;
    z-index: 2;
  }

  &__heading {
    margin-top: 30px;
    position: relative;
    z-index: 2;

    @media (min-width: $large-breakpoint) {
      margin-top: 35px;
    }
  }

  &__text {
    margin-top: 1rem;
    position: relative;
    z-index: 2;

    @media (min-width: $large-breakpoint) {
      margin-top: 0.6rem;
    }
  }

  &__img-block {
    margin: 35px auto 0;
    height: 60px;
    display: flex;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    position: relative;
    z-index: 2;
    padding-left: 1rem;
    padding-right: 1rem;
    box-sizing: border-box;
    max-width: 600px;

    @media (min-width: $medium-breakpoint) {
      margin: 50px auto 0;
      height: auto;
      padding-left: 2.5rem;
      padding-right: 2.5rem;
      max-width: 1024px;
    }

    @media (min-width: $large-breakpoint) {
      flex-wrap: wrap;
      padding-left: 3.5rem;
      padding-right: 3.5rem;
      max-width: 1440px;
    }
  }

  &__img {
    height: 30px;
    width: auto;
    display: inline-block;
    margin-right: 30px;
    flex-grow: 0;
    flex-shrink: 0;
    user-select: none;
    object-fit: contain;

    &:last-of-type {
      margin-right: 0;
      padding-right: 1rem;
    }

    @media (min-width: $medium-breakpoint) {
      height: 35px;
      margin-right: 72px;
      margin-bottom: 30px;

      &:last-of-type {
        padding-right: 2.5rem;
      }
    }

    @media (min-width: 1024px) {
      margin-right: 30px;
    }

    @media (min-width: $large-breakpoint) {
      margin-bottom: 37px;
      margin-right: auto;

      &:last-of-type {
        padding-right: 0;
      }
    }
  }

  &__link-wrapper {
    @mixin container;

    padding-bottom: 39px;
    margin-top: 10px;

    @media (min-width: $medium-breakpoint) {
      padding-bottom: 71px;
      margin-top: 20px;
    }

    @media (min-width: $large-breakpoint) {
      padding-bottom: 72px;
    }
  }
}
