.page-footer {
  background: $midnight;

  &__inner {
    @mixin container;

    padding-top: 38px;
    padding-bottom: 39px;

    @media (min-width: $medium-breakpoint) {
      padding-top: 70px;
    }

    @media (min-width: $footer-medium-breakpoint) {
      padding-top: 67px;
      padding-bottom: 10px;
    }

    @media (min-width: $large-breakpoint) {
      padding-top: 55px;
      padding-bottom: 12px;
    }
  }

  &__container {
    @media (min-width: $medium-breakpoint) {
      display: flex;
    }
  }

  &__content {
    @media (min-width: $medium-breakpoint) {
      margin-left: 80px;
    }

    @media (min-width: $footer-medium-breakpoint) {
      margin-left: 0;
      order: -1;
      padding-top: 4px;
      padding-right: calc(3% + .5rem);
      padding-left: 0;
    }
  }

  &__logo {
    margin-left: -1px;
  }

  &__contact-info {
    margin-top: 20px;

    @media (min-width: $medium-breakpoint) {
      margin-top: 12px;
    }
  }
}
